 
 const todoListReducer = (state = [],action) => {
    switch (action.type) {
      case 'ADD_TODO':
        state = [...state,action.payload]  
        return state;
      case 'DELETE_TODO':
        const list = [...state]
        list.splice(action.payload,1)
        state = list;
        return state;
      default:
        return state;
    }
  }
export default todoListReducer;