import React, { Component } from 'react';
import { connect } from 'react-redux';
import { addTodo } from '../actions';
import "../Functional/App.css";
import Task from './Task';

class TodoClass extends Component {
  constructor(props) {
    super(props);
    this.state = { 
      task : "",
    }

  }
  
  updateTask(value) { 
    this.setState({ task:value })
  }
  addTasks(value) {
    this.props.addTodo(value)
    this.setState({ task:"" })
  }


  render() { 
    return ( 
      <div className="App">
        class component <hr></hr>
          <div className="add_task">
              <input 
              type="text" 
              placeholder="Enter task here" 
              value={this.state.task}
              onKeyPress={(e) => {
                if (e.code === "Enter") {
                  this.addTasks(this.state.task);
                }
              }}
              onChange={e => this.updateTask(e.target.value)}
              />
              <input 
              className="add_button" 
              type="button" 
              value="+" 
              onClick={() => this.addTasks(this.state.task)}
              />
          </div>
          <div className="list_task">
              {this.props.todos.length ?
               this.props.todos.map((item,index) => (
                  <Task key={index} index={index} message={item}  />                
              )) : <div> empty todo </div>}
          </div>
      </div>  
    );
  }
}
 const mapStateToProps = (state) => {
   return {
     todos : state.todoList
   }
 }
 const mapDispatchToProps = (dispatch) => {
    const addNewTodo = (item) => {
      dispatch(addTodo(item))
    }
    return {
      addTodo : addNewTodo,
    }
  }
export default connect(mapStateToProps,mapDispatchToProps)(TodoClass);