import { useState } from "react";
import './styles.css'
const TicaTacToe = () => {
  const [gamelist,setGamelist] = useState(["","","","","","","","",""])
  const updateGameList = (index) => {
    let list = [...gamelist]
    list[index] = "X"
    console.log(" list ",list)
    setGamelist(list)
  }
  return ( 
    <div>
      Game Here
      <div className="container">
        {gamelist.map((item,index)=>(
          <div className="element" onClick={()=>updateGameList(index)} key={index}>{item}</div>
        ))}
      </div>
    </div> 
  );
}
 
export default TicaTacToe;