import { useDispatch } from 'react-redux';
import { deleteTodo } from '../actions';
import './App.css';

function Task(props) {
  const dispatch = useDispatch();
  const deleteTasks = (index) => {
    dispatch(deleteTodo(index))
  }
  return (
    <div className="task">
      <input type="checkbox" />
      {props.message}
      <input 
      className="delete_button"
      type="button" 
      value="X"
      onClick={()=> deleteTasks(props.index)}
      />
    </div>
  );
}

export default Task;
